}

.text-yellow {
  color: #a98921;
}

.text-green {
  color: #3b9668;
}

.cont {
  display: grid;
  height: 100vh;
  grid-template-columns: 0.8fr 1fr 1fr 1fr;
  grid-template-rows: 0.2fr 3fr;
  grid-template-areas:
    "sidebar nav nav nav"
    "sidebar main main main";
}

@media only screen and (max-width: 978px) {
  .cont {
    grid-template-columns: 1fr;
    grid-template-rows: 0.2fr 3fr;
    grid-template-areas:
      "nav"
      "main";
  }
}

.navbar {
  background: #000000;
  grid-area: nav;
  display: block !important;
  margin-bottom: 0px !important;
  padding-bottom: 0px !important;
  height: 20px;
  width: 300px;
  margin-left: 300px;
  /* padding: 0 30px 0 30px; */
}
.nav-bar-links-container{
  color: #ffffff
}

.nav-bar-search input{
  background-color:#101010;
  outline: none;
}
.nav-bar-search i{
  color: #ffffff;
  background-color:#101010;
  outline: none;
}
.nav-bar-search span{
  color: #ffffff;
  background-color:#101010;
  outline: none;
}
.search-text:focus{
  background-color: #101010;
  color: #ffffff;
  border-color: #101010;
  border: 0px solid #101010;
}
.input-group span{
  border: 0px solid #101010;
}
.input-group{
  border: 0px solid #101010;
  width: 50vw;
}
.input-group input{
  border: 0px solid #101010;
}

@media (max-width: 400px) {
  .navbar {
    margin-left: 0px;
    /* padding: 0 30px 0 30px; */
  }
}

#sidebar {
  background: #101010;
  grid-area: sidebar;
  width: 320px;
  height: 450px;
  padding: 20px;
  transition: all 0.5s;
  float: left;
  margin-right: 30px;
  min-height: calc(100vh);
}

.sidebar__title {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f3f4f6;
  margin-bottom: 30px;
  /* color: #E85B6B; */
}

.sidebar__img img {
  width: 150px;
  height: 150px;
  border-radius: 0px !important;
}

/* .sidebar__title > div > img {
  width: 75px;
  object-fit: contain;
} */

.sidebar__title > div > h1 {
  font-size: 18px;
  display: inline;
}

.sidebar__title > i {
  font-size: 18px;
  display: none;
}

.sidebar__menu > h2 {
  color: #3ea175;
  font-size: 16px;
  margin-top: 15px;
  margin-bottom: 5px;
  padding: 0 10px;
  font-weight: 700;
}

.sidebar__link {
  color: #f3f4f6;
  padding: 10px;
  border-radius: 3px;
  margin-bottom: 5px;
  position: relative;
  cursor: pointer;
}
.sidebar__link:hover{
  background: linear-gradient(to left, black , yellow);
  transition: 0.9s ease;
}
.active_menu_link {
  background: black;
  background: linear-gradient(to left, black , yellow);
  color: #FFFFFF;
}

.active_menu_link a {
  color: #FFFFFF !important;  
}

.sidebar__link > a {
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 700;
  margin-left: 10px;
}

.sidebar__link > i {
  margin-right: 10px;
  font-size: 18px;
}

.sidebar__logout {
  margin-top: 20px;
  padding: 10px;
  color: #e65061;
}

.sidebar__logout > a {
  text-decoration: none;
  color: #e65061;
  font-weight: 700;
  text-transform: uppercase;
}
.sidebar__logout > a:hover{
  background: linear-gradient(to left, black , yellow);
  transition: 0.9s ease;
}


.sidebar__logout > i {
  margin-right: 10px;
  font-size: 18px;
}

.sidebar_responsive {
  display: inline !important;
  z-index: 9999 !important;
  left: 0 !important;
  position: absolute;
}
.sidebar-container{
  display: flex !important;
}
.icon-container{
  justify-content: end;
  padding-right: 10px;
  width: 15px;
}
.link-container > a{
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 700;
  margin-left: 10px;
}

.game-icon {
  width: 18px;
  height: 15px;
  border-radius: 0px;
}

@media only screen and (max-width: 978px) {
  #sidebar {
    display: none;
  }

  .sidebar__title > i {
    display: inline;
  }
}

