.table-wrapper { 
    overflow: hidden;   
    margin-right: 10px;    
}

th {
    color: #AAAAAA !important;
    font-weight: lighter;
    border-bottom: #707070 1px solid !important;
    padding: 20px !important;

}

table {
    background-color: #101010 !important;
    border-radius: 5px;
}

td {
    color: white;
    border: 0 !important;
    padding: 20px !important;
}

.table-tr {
    border: 0 !important;
    padding: 20px !important;
}

tbody tr:hover {
    cursor: pointer;
    border: 1px white solid !important;
}

