.pos-card {
    /* background-color: #707070; */
    border-radius: 10px ;
    width: 320px;
    height: 220px;
    color: white;
    float: left;
    margin-right: 10px;
}

.pos-card-img {
    border-radius: 50%;
    width: 100px;
    height: 100px;
}

.img-border-1 {
    border: 2px solid #FFE600;
}

.img-border-2 {
    border: 2px solid #CB0000;
}

.img-border-3 {
    border: 2px solid #E272FD;
}

.crown {
    margin-left: 18px;
}

.img-sec {
    padding: 1rem 3rem !important;
}

.pos-icon {
    position: relative;
    top: -15px;
    left: 30px;
    /* padding-bottom: 3px ; */
}

@media screen and (max-width: 775px) {
    .crown {
        position: relative;
        top: -70%;
        left: 26%;
    }

    .pos-icon {
        position: relative;
        top: 50px;
        left: -77px; 
        padding-bottom: 3px;
    }

    .img-sec {
        padding: 30px 30px 0 30px;
        margin-top: 20px;
    }
}
