.battle-card{
    background-color: #101010;
    border-radius: 8px;
}

.labels{
    font-size: 12px;
    color: #AAAAAA;
}
.values {
    font-size: 12px;
    color: white;
}

.battle-diamond-icon image {
    width: 65%;
}

.dot {
    height: 100px;
    width: 100px;
    background-color: transparent;
    border: 1px grey solid;
    border-radius: 50%;
    margin-top: 22px;
    margin-left: 20px;
    color: #FFE600;
    font-weight: bold;
    font-size: 10px;
}

.available{
    color: #AAAAAA;
    font-size: 11px;
}
.edit{
    color: #FFF;
    font-size: 11px;
    margin-left: 50px;
}
.edit:hover{
text-decoration: none;

}

@media screen and (max-width: 775px) {
    .dot {
        margin-top: 50px;
        margin-left: 0px;
    }

    .circle-sec {
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .battle-card{
       max-width: fit-content;
    }

    .body-sec {
        padding-left: 20px !important;
    }
}
