.main-heading {
    font-weight: bold;
    color: white;
    font-size: 16px;
}

.sub-headings {
    color: white;
}

.form-input {
    background-color: #101010;
    border: none;
    color:white
}

.form-input:focus {
    background-color: #101010;
    border-style: none;
    color:white

}

.form-btn{
    border: 1px #FFE600 solid;
    background-color: transparent !important;
    text-decoration: none !important;
    color: white;
}

.form-btn-create{
    background-color: #FFE600 !important;
    text-decoration: none !important;
}

.form-btn:hover{
    cursor: pointer;
    text-decoration: none !important;
    border: 1px #FFE600 solid;
}

.btn-group {
    padding: 0 !important;
}

input[type=file]::file-selector-button {
    background-color: rgb(255, 230, 0, 0.1);
    font-size: 12px;
    padding: 5px;
    color: white;
    border: 0;
    border-radius: 4px;
}

.input-icons i {
    position: absolute;
}
  
.input-icons {
    width: 100%;
    margin-bottom: 10px;
}
  
.icon {
    padding: 10px;
    min-width: 40px;
}
  
.input-field {
    width: 100%;
    padding: 10px;
    text-align: center;
}   

.bet {
    background-color: #101010;
    border-radius: 3px;
}

.diamond-icon image {
    width: 80%;
}

