.navbar {
  background: #000000;
  grid-area: nav;
  display: block !important;
  margin-bottom: 0px !important;
  padding-bottom: 0px !important;
  height: 20px;
  width: 300px;
  margin-left: 300px;
  /* padding: 0 30px 0 30px; */
}
.nav-bar-links-container{
  color: #ffffff
}

.nav-bar-search input{
  background-color:#101010;
  outline: none;
}
.nav-bar-search i{
  color: #ffffff;
  background-color:#101010;
  outline: none;
}
.nav-bar-search span{
  color: #ffffff;
  background-color:#101010;
  outline: none;
}
.search-text:focus{
  background-color: #101010;
  color: #ffffff;
  border-color: #101010;
  border: 0px solid #101010;
}
.input-group span{
  border: 0px solid #101010;
}
.input-group{
  border: 0px solid #101010;
  width: 50vw;
}
.input-group input{
  border: 0px solid #101010;
}

@media (max-width: 400px) {
  .navbar {
    margin-left: 0px;
    /* padding: 0 30px 0 30px; */
  }
}
