.table-wrapper { 
    width: 75%;
    margin-right: 10px;
    float: left;    
}

.table-main-heading {
    font-weight: 400;
    color: white;
    font-size: 16px;
}

th {
    color: #AAAAAA !important;
    font-weight: lighter;
    border-bottom: #707070 1px solid !important;
    padding: 20px !important;

}

table {
    background-color: #101010 !important;
    border-radius: 5px;
}

td {
    color: white;
    border: 0 !important;
    padding: 20px !important;

}

.detail-tr {
    border: 0 !important;
}

img {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-right: 10px;
}

.points {
    color: #D7FF03;
    padding-right: 5px;
}

