.game-card{
    background-color: #101010;
    margin : 10px;
    padding : 10px;
    padding-left: 0px !important;
    border-radius: 10px;
}
.game-card img{
    background-color: #101010;
    border: 0px solid #101010;
}
.game-card p{
    font-weight: 500;
}
.game-card i{
    color: #FFE600;
}
.game-card-link{
    font-weight: 500;
    background-color: #101010;
    border: 1px solid #424040;
    color: white;
}
.game-card-link:hover{
    font-weight: 500;
    background-color: #000000;
    border: 1px solid #424040;
    color: white;
}