body{
    background-color: black;
}
.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .main-background{
    height: 100vh;
    position: absolute;
    background-image: url('./assets/images/background.png')
  }
  .login-form-container{
    background-color: white;
  }
  .logo-icon-area{
    margin-top :calc(100% - 80%);
  }
  .logo-icon-area img{
    height: 275px;
    width: 265px;
  }
  .logo-icon-area h1{
    color: #61dafb;
    font-size: 14px;
  }
  .welcome-span{
    font-size: 20px;
    font-weight: bold;
  }
  .text-in-form{
    color: #AAAAAA;
    font-size: 12px;
  }
  
  .auth-form-input-fields input{
    margin-top: 20px;
    /* width: 400px; */
    max-width: 100%;
    
  }
  .auth-submit-btn button{
    margin-top: 50px;
    height: 40px;
    width: 200px;
    max-width: 100%;
    font-weight: bold;
  }
  .container-remember{
    position: absolute;
    margin-top: 5px;
  }
  .container-remember span{
    margin-left: 2px;
  }
  .fields-container{
    min-width: 100%;
    max-width: 100%;
    margin-left: auto;
      margin-right: auto;
  }
  .side-bar{
    min-height: 100vh;
    background: #101010;
    position:relative ;
    top: 0;
  }
  .side-bar .menu{
    width: 100%;
  }
  .side-bar .menu .item{
    position: relative;
    cursor: pointer;
  }
  .side-bar .menu .item a{
    color:#FFFFFF;
    font-size: 16px;
    text-decoration: none;
    display: block;
    padding: 5px 30px;
    line-height: 30px;
  }
  .side-bar .menu .item a:hover{
    background: linear-gradient(to left, black , yellow);
    transition: 0.9s ease;
  }
  .side-bar .menu .item i{
   margin-right: 15px;
  }
  
  .side-bar .menu .close-btn{
    position: relative;
    cursor: pointer;
  }
  
  .side-bar .menu .close-btn a{
    color:#FFFFFF;
    font-size: 16px;
    text-decoration: none;
    display: block;
    padding: 5px 30px;
    line-height: 30px;
  }
  
  .menu{
    align-items: left;
  }
  .menu .item a{
    text-align: left !important ;
  }
  
  .menu-btn{
    position: absolute;
    cursor: pointer;
  }
  .menu-btn a:hover{
    color: #FFE600;
    transition: 0.3s ease;
    
  }
  .menu-btn a{
    color:#FFFFFF;
    font-size: 16px;
    text-decoration: none;
    display: block;
    padding: 5px 30px;
    line-height: 30px;
  }
  .close-btn a{
    text-align: end !important ;
    margin-right: 5px;
  }
  .main-dashboard{
    background-color: #101010;
    max-width: 100%;
  }
  .side-bar-container{
    background-color: #101010;
    height: 100vh;
  }
  .main-dashboard-container{
    background-color: #000000;
    height: 100vh;
  }
  
  .custom-sidebar-col{
    width: 16.33333%;
  }
  .custom-main-dashboard-col{
    width: 83.6667%;
  }
  .has-search .form-control {
    padding-left: 2.375rem;
  }
  .has-search .form-control-feedback {
    position: absolute;
    z-index: 2;
    display: block;
    width: 2.375rem;
    height: 2.375rem;
    line-height: 2.375rem;
    text-align: center;
    pointer-events: none;
    color: #aaa;
  }
  
  /* TOPBAR */
  .topbar-container i{
    color: #FFFFFF;
  }
  .topbar-container span{
    color: #FFFFFF;
  }
  
  /* Events Card */
  .events-container {
  color: white;
  }
  .events-container {
    background-color: black;
    margin-top: 10px;
  }
  .card {
    background-color: #101010;
  }
  .image-container{
    width: 90px;
    height: 95px;
  }
  .event img{
    width: 90%;
    height: 90%;
  }